@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Inconsolata:wght@400;500&family=Noto+Serif&family=Rubik&display=swap);
:root {
  --color: rgb(0, 255, 200);
}

.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  font-size: 18px;
  font-weight: bold;
  color: #222;
  background-color: rgb(0, 255, 200);
  background-color: var(--color);
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn:hover {
  padding: 6px 18px;
  background-color: #444;
  color: rgb(0, 255, 200);
  color: var(--color);
  border-radius: 4px;
  border: 2px solid #444;
}
.navbar {
  background: rgb(27, 27, 27);  
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.navbar-logo:hover {
  color: #ee0e0e;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav-links:hover {
  background-color: #2dbdbd;
  color: #242222;
  border-radius: 4px;
  transition: all 0.3s ease-out;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
  color: #18f0ff;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    padding: 0%;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.3s ease;
  }

  .nav-menu.active {
    background: #242222;
    height: 70%;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #18ffd8;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 20px;
    margin: 3rem auto;
    border-radius: 4px;
    width: 20%;
    margin-bottom: 60px;
    background: #065a41;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #1888ff;
    transition: 250ms;
  }

  button {
    display: none;
  }
}
